<template>
  <!--상품 목록-->
  <div class="swiperWrap swiperDivi swipeType04" data-slide-per-view="auto">
    <div class="swiper-container">
      <button type="button" class="swiper-button-prev">이전</button>
      <ul class="swiper-wrapper">
        <li v-for="(item, index) in parsedData" v-bind:key="index" class="swiper-slide">
          <div>
            <a @click="click('이동', item)">
              <span class="swImgs">
                <span v-if="item.dcType === 'type1'" class="bdg type01" v-html="$t('product.수량할인')"></span>
                <span v-if="item.dcType === 'type2'" class="bdg type02" v-html="$t('product.믹스매치')"></span>
                <span v-if="item.dcType === 'type3'" class="bdg type03" v-html="$t('product.묶음할인')"></span>
                <img :src="item.imgUrl" alt=""/>
               <span v-if="item.soldOutYn === 'Y'" class="soldOut" >{{$t('product.일시품절')}}</span>
              </span>
              <span class="swInfo">
                <span class="prdTit">{{item.brandNm}} &middot; {{item.enBrandNm}}</span>
                <span class="prdName">{{item.goosNm}}</span>

                <span class="price" v-if="$lang === $dictionary.languageType.CN">
                  <span class="originPrice" v-if="item.dcRate !== '10.0'" aria-label="정가">${{ numberWithCommas(item.costPrc) }}</span>
                  <span class="originPrice" v-else aria-label="정가" />
                  <span class="mon01">
                    <b class="saleRate" v-if="item.dcRate !== '10.0'">{{item.dcRate}}折</b>
                    <b class="salePrice">${{numberWithCommas(item.dcDolrPrc)}}</b>
                  </span>
                  <span class="mon02">￥<span>{{numberWithCommas(item.dcSellPrc)}}</span></span>
                </span>

                <span class="price" v-else>
                  <span class="originPrice" v-if="item.dcRate !== '0.0'" aria-label="정가">${{ numberWithCommas(item.costPrc) }}</span>
                  <span class="originPrice" v-else aria-label="정가" />
                  <span class="mon01">
                    <b class="saleRate" v-if="item.dcRate !== '0.0'">{{item.dcRate}}%</b>
                    <b class="salePrice">${{numberWithCommas(item.dcDolrPrc)}}</b>
                  </span>
                  <span class="mon02"><span>{{numberWithCommas(item.dcSellPrc)}}</span>원</span>
                </span>
              </span>
            </a>
          </div>
        </li>
        <!--캐러셀 항목 5개 이상일시에만 노출-->
        <li v-if="data.list.length > 5 && show_more" class="swiper-slide btnMors">
          <button @click="click('더보기')" type="button" class="btMore">{{ $t("common.더보기") }}</button>
        </li>
      </ul>
      <button type="button" class="swiper-button-next">다음</button>
    </div>
  </div>
  <!--//상품 목록-->
</template>
<style scoped>
.originPrice {
  text-decoration: line-through;
}

.originPrice:before {
  content: '\200b'
}

.saleRate {
  padding-right: 6px !important;
  color: #f42d10 !important;
}

.salePrice {
  color: #373a3c;
  font-weight: 600;
}

.mon02 {
  font-size: 1.3rem !important;
  color: #6b6e73;
}
</style>

<script>

export default {
  name: 'ProductCard',
  props: {
    data: {},
  },
  mounted() {
    this.$uiHandler.swiperNextPrevShow();
  },

  computed: {
    parsedData() {
      if (this.data.list.length > 5) {
        return this.data.list.slice(0, 5);
      }

      return this.data.list;
    },
    show_more() { // 더보기 버튼 노출여부
      return this.data.show_more;
    },
  },
  filters: {
  },
  methods: {
    click(id, item) {
      switch (id) {
        case '이동':
          window.cbMoveParent(item.goosUrl);
          this.$analyticsHandler.apply(item.tagName, { tag_cd: item.goosCd });
          break;
        case '더보기':
          window.cbMoveParent(this.data.moreUrl);
          this.$analyticsHandler.apply(this.data.tagName);
          break;
        default:
          break;
      }
    },
    numberWithCommas(x) {
      if (x == null) return '';
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    },
  },
};
</script>